html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "coder";
  src: local("coder"), url('./font.woff') format("woff");
}

body {
  margin: 0;
  font-family: "coder", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body::-webkit-scrollbar {
  display: none;
}

body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

html {
  background-color: "#4C4C4C";
}

div {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
}

.wrapper-main{
  background:#111;
  min-height:100vh;
  width:100vw;
  /* padding:20px 20px; */
  background-image: url('../redux/data/background.png');
  background-position: center;
  background-size: cover;
}

p{
  margin:0;
  padding: 0;
}
.div-center{
  height:100%;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button{
  padding: 10px 30px;
  border: none;
  color: white;
  background: black;
  cursor: pointer; 
  border-radius: 30px;
}
.gap-right{
  margin-right: 50px;
}
.button-selected{
  padding: 10px 30px;
  border: none;
  color: white;
  background: black;
  cursor: pointer; 
  border: 2px solid red;
  border-radius: 30px;

}
.inner-wrapper{
  height: auto;
  width: 70%;
  background: rgb(255, 255, 255);
  padding: 30px;
  border: 5px solid black;
}
.nav-link{
  margin-right: 10px;
}
@media only screen and (max-width: 580px) {
  .nav-link{
    margin-right: 5px;
  }
  .inner-wrapper{
    height: auto;
    width: 340px;
    background: rgb(255, 255, 255);
    padding: 30px;
    border: 5px solid black;
  }

}